
.scenic_lists{
  background-color:#f0f0f0;
  /*padding-bottom: 10px;*/
}
.scenic_list{
  padding: 5px 4px 0px 4px;
  background: #fff;
}
.scenic_img{
  position: relative;
}
/*.scenic_img i{
  background: url(../assets/images/z-yy-icon.png) no-repeat;
  background-size: 100%;
  display: block;
  width: 4rem;
  height: 4rem;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -2rem;
  margin-top: -2rem;
}*/
.scenic_img img{
  display: block;
  width: 100%;
  border-radius: 5px;
  height: auto;
}
.scenic_bt{
  position: absolute;
  top: 1rem;
  left: 0;
  width: 100%;
  padding:0 1rem;
  text-shadow: 1px 1px 1px rgba(0,0,0,0.3)
}
.scenic_t h4{
  color: #fff;
  font-weight: bold;
  font-size: 1.1rem;
}
.scenic_t h4 i {
    display: inline-block;
    border: 1px solid #fff;
    color: #fff;
    border-radius: 8px;
    font-size: .5rem;
    font-weight: normal;
    padding: 0px 8px;
    height: 1rem;
    line-height: .9rem;
    margin-left: 10px;
    position: relative;
    top: -2px;
}
.scenic_t b{
  display: inline-block;
}
.scenic_t b img{
  width: .5rem;
  display: block;
}
.scenic_b{
  padding: 10px 0;
}
.scenic_b span{
  font-size: 12px;
  color: #f6f6f6;
  margin-right: .5rem;
}
.scenic_b span img{
  width: .7rem;
  display: inline-block;
}



