
.home{
  width: 100%;
  max-width: 768px;
  margin: 0 auto;
  min-height: 100vh;
  overflow: auto;
}
.h_top{
  width: 100%;
  position: relative;
  color: #fff;
  font-weight:bold;
  max-width: 768px;
  margin: 0 auto;
}
.h_top_img{
  width: 100%;
}
.h_top_img img{
  width: 100%;
  display: block;
  height: auto;
}
.h_top_box{
  position: absolute;
  width:100%;
  left: 0;
  top: 0;
  padding: 0 20px;
}
.h_top_w{
  margin-top:10%;
  position: relative;
}
.city_ct{
  display: block;
}
.city_ct span{
    font-size: 1.4rem;
    color: #fff;
}
.city_ct img{
    width: 1rem;
    margin-left: 10px;
    vertical-align: middle;
    margin-top: -10px;
}
.city_c{
    margin-bottom: 1rem;
}
.city_e span{
    font-size: 1rem;
    font-weight: normal;
}
.city_star img{
    width: .8rem;
}
.want_go{
    position: absolute;
    right: 0;
    top: 50%;
    margin-top: -1.5rem;
}
.want_go img{
    width: 3rem;
}
.n_top_search{
    position: absolute;
    width: 100%;
    left: 0;
    bottom: 3rem;
    padding: 0 20px;
    height: 2rem;
    line-height: 2rem;
    overflow: hidden;
    margin-top: 3rem;
}
.n_top_search input{
    width: 75%;
    padding: 0px 10px;
    float: left;
    height: 2rem;
    line-height: 2rem;
    border-radius: 1rem 0 0 1rem;
    background: none;
    border: 1px solid #DDD;
    border-right: 0px;
    outline: none;
    color: #FFF;
    background: rgba(0,0,0,0.3);
}
.n_top_search button{
    background: #19cb8f;
    width: 25%;
    height: 2rem;
    border: none;
    outline: none;
    border-radius:0 1rem 1rem 0;
}
.n_top_search button img{
    width: 1rem;
    margin-left: -.5rem;
}
.home_main{
    width: 100%;
    max-width: 768px;
    margin: 0 auto;
}



