
.pay{
  width: 100%;
  height: 100%;
  background-color: #f9f9f9;
  max-width: 768px;
  margin:0 auto;
  min-height: 100vh;
}
.payhead{
  height: 3rem;
  line-height: 3rem;
  background: #11cd8d;
  color: #fff;
  text-align: center;
  position: relative;
  font-size: 1.2rem;
  width: 100%;
}
.payhead b{
  height: 1.5rem;
  width: .7rem;
  left:1rem;
  display: inline-block;
  background:url(../assets/images/dis-icon03.png) no-repeat center;
  background-size: 100%;
  position: absolute;
  top: .75rem;
}
.payDetail{
  padding: .6rem;
  background: #fff;
}
.payDetail>div:not(:last-child){
  margin-bottom: .5rem;
}
.payDetail>div span{
  display: inline-block;
  width: 6rem;
  color: #999;
}
.payDetail>div i{
  color:#E64340;
}
.payWX,.payZFB{
  width: 100%;
  height: 4rem;
  line-height: 4rem;
  padding: .5rem;
  background: #fff;
  margin-top: 3rem;
  position: relative;
  cursor: pointer;
}
.payWX img,.payZFB img{
  height: 3rem;
  width: 3rem;
}
.payWX span, .payZFB span{
  display: inline-block;
  height: 3rem;
  line-height: 3rem;
  vertical-align: top;
  margin-left: 2rem;
}
.payWX i,.payZFB i{
  position: absolute;
  width: 1rem;
  height: 2rem;
  background:url(../assets/images/z-icon-06.png) no-repeat center;
  top:50%;
  right: 2%;
  margin-top: -1rem;
}
.payZFB{
  margin-top: 1rem;
}



