
.my-switch {  
    position: relative;   
    width: 25px;  
    margin: 0;  
    -webkit-user-select:none;   
    -moz-user-select:none;   
    -ms-user-select: none;
}
.my-switch-checkbox {  
    display: none;
}
.my-switch-label {  
    display: block;   
    overflow: hidden;   
    cursor: pointer;  
    border: 1px solid #d3d3d3;   
    border-radius: 20px;
}
.my-switch-inner {  
    display: block;   
    width: 200%;   
    margin-left: -100%;  
    -webkit-transition: margin 0.3s ease-in 0s;  
    transition: margin 0.3s ease-in 0s;
}
.my-switch-inner::before, .my-switch-inner::after {  
    display: block;   
    float: right;   
    width: 50%;   
    height: 13px;   
    padding: 0;   
    line-height: 13px;  
    font-size: 12px;   
    color: white;   
    font-family:   
    Trebuchet, Arial, sans-serif;   
    font-weight: bold;  
    -webkit-box-sizing: border-box;  
            box-sizing: border-box;
}
.my-switch-inner::after {  
    content: attr(data-on);  
    padding-left: 4px;  
    background-color: #31d4ff;   
    color: #FFFFFF;
}
.my-switch-inner::before {  
    content: attr(data-off);  
    padding-right: 4px;  
    background-color: #f1f1f1;   
    color: #999999;  
    text-align: right;
}
.my-switch-switch {  
    position: absolute;   
    display: block;   
    width: 15px;  
    height: 15px;  
    margin: 0px;  
    background: #FFFFFF;  
    top: 0;   
    bottom: 0;  
    right: 12px;  
    border: 1px solid #d3d3d3;   
    border-radius: 20px;  
    -webkit-transition: all 0.3s ease-in 0s;  
    transition: all 0.3s ease-in 0s;
}
.my-switch-checkbox:checked + .my-switch-label .my-switch-inner {  
    margin-left: 0;
}
.my-switch-checkbox:checked + .my-switch-label .my-switch-switch {  
    right: 0px;
}



